<template>
	<div>
		<el-dialog modal-class="edit_form_modal" class="edit_form" v-model="formVisible" :title="formTitle" width="60%" destroy-on-close :fullscreen='false'>
			<el-form class="formModel_form" ref="formRef" :model="form" :rules="rules">
				<el-row>
					<el-col :span="12">
						<el-form-item label="" prop="shourumingcheng">
							<el-input class="list_inp" v-model="form.shourumingcheng" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.shourumingcheng?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="shourujine">
							<el-input class="list_inp" v-model.number="form.shourujine" placeholder=""
								 type="number" 								:readonly="!isAdd||disabledForm.shourujine?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="shourubeizhu">
							<el-input class="list_inp" v-model="form.shourubeizhu" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.shourubeizhu?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="zhichumingcheng">
							<el-input class="list_inp" v-model="form.zhichumingcheng" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.zhichumingcheng?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="zhichujine">
							<el-input class="list_inp" v-model.number="form.zhichujine" placeholder=""
								 type="number" 								:readonly="!isAdd||disabledForm.zhichujine?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="zhichushuoming">
							<el-input class="list_inp" v-model="form.zhichushuoming" placeholder=""
								 type="text" 								:readonly="!isAdd||disabledForm.zhichushuoming?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="" prop="shouzhijieyu">
							<el-input class="list_inp" v-model="shouzhijieyu" :readonly="true" placeholder="" />
						</el-form-item>
					</el-col>
					<el-col :span="12">
						<el-form-item label="" prop="dengjiriqi">
							<el-date-picker
								class="list_date"
								v-model="form.dengjiriqi"
								format="YYYY  MM  DD "
								value-format="YYYY-MM-DD"
								type="datetime"
								:readonly="!isAdd||disabledForm.dengjiriqi?true:false"
								placeholder="" />
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="" prop="dengjineirong">
							<el-input v-model="form.dengjineirong" placeholder="" type="textarea"
							:readonly="!isAdd||disabledForm.dengjineirong?true:false"
							/>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<template #footer v-if="isAdd||type=='logistics'||type=='reply'">
				<span class="formModel_btn_box">
					<el-button class="cancel_btn" @click="closeClick"></el-button>
					<el-button class="confirm_btn" type="primary" @click="save"
						>
						
					</el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>
<script setup>
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		computed,
		defineEmits
	} from 'vue'
    import {
        useStore
    } from 'vuex';
    const store = useStore()
    const user = computed(()=>store.getters['user/session'])
	const context = getCurrentInstance()?.appContext.config.globalProperties;	
	const emit = defineEmits(['formModelChange'])
	//
	const tableName = 'caiwuxinxi'
	const formName = ''
	//
	//form
	const form = ref({})
	const disabledForm = ref({
		shourumingcheng : false,
		shourujine : false,
		shourubeizhu : false,
		zhichumingcheng : false,
		zhichujine : false,
		zhichushuoming : false,
		shouzhijieyu : false,
		dengjiriqi : false,
		dengjineirong : false,
	})
	const formVisible = ref(false)
	const isAdd = ref(false)
	const formTitle = ref('')
	//
	//
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("URL"));
		} else {
			callback();
		}
	}
	const rules = ref({
		shourumingcheng: [
		],
		shourujine: [
			{required: true,message: '',trigger: 'blur'}, 
			{ validator: validateNumber, trigger: 'blur' },
		],
		shourubeizhu: [
		],
		zhichumingcheng: [
		],
		zhichujine: [
			{required: true,message: '',trigger: 'blur'}, 
			{ validator: validateNumber, trigger: 'blur' },
		],
		zhichushuoming: [
		],
		shouzhijieyu: [
			{ validator: validateNumber, trigger: 'blur' },
		],
		dengjiriqi: [
		],
		dengjineirong: [
		],
	})
	//
	
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	const shouzhijieyu =computed(()=>{
		let c = form.value
		let a = c.shourujine-c.zhichujine
        form.value.shouzhijieyu = Number(a)?Number(parseFloat(a).toFixed(2)) : 0
        return Number(a)?Number(parseFloat(a).toFixed(2)) : 0
	})
	//methods

	//
	const getUUID =()=> {
      return new Date().getTime();
    }
	//
	const resetForm = () => {
		form.value = {
			shourumingcheng: '',
			shourujine: '',
			shourubeizhu: '',
			zhichumingcheng: '',
			zhichujine: '',
			zhichushuoming: '',
			shouzhijieyu: '',
			dengjiriqi: '',
			dengjineirong: '',
		}
	}
	//info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
			formVisible.value = true
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//
	const init=(formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null)=>{
		resetForm()
			form.value.dengjiriqi = context?.$toolUtil.getCurDate()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
			formTitle.value = '' + formName
			formVisible.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			formTitle.value = '' + formName
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			formTitle.value = '' + formName
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			formTitle.value = formNames
			// getInfo()
			for(let x in row){
				if(x=='shourumingcheng'){
					form.value.shourumingcheng = row[x];
					disabledForm.value.shourumingcheng = true;
					continue;
				}
				if(x=='shourujine'){
					form.value.shourujine = row[x];
					disabledForm.value.shourujine = true;
					continue;
				}
				if(x=='shourubeizhu'){
					form.value.shourubeizhu = row[x];
					disabledForm.value.shourubeizhu = true;
					continue;
				}
				if(x=='zhichumingcheng'){
					form.value.zhichumingcheng = row[x];
					disabledForm.value.zhichumingcheng = true;
					continue;
				}
				if(x=='zhichujine'){
					form.value.zhichujine = row[x];
					disabledForm.value.zhichujine = true;
					continue;
				}
				if(x=='zhichushuoming'){
					form.value.zhichushuoming = row[x];
					disabledForm.value.zhichushuoming = true;
					continue;
				}
				if(x=='shouzhijieyu'){
					form.value.shouzhijieyu = row[x];
					disabledForm.value.shouzhijieyu = true;
					continue;
				}
				if(x=='dengjiriqi'){
					form.value.dengjiriqi = row[x];
					disabledForm.value.dengjiriqi = true;
					continue;
				}
				if(x=='dengjineirong'){
					form.value.dengjineirong = row[x];
					disabledForm.value.dengjineirong = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			formVisible.value = true
		}

		context?.$http({
			url: `${context?.$toolUtil.storageGet('sessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(context?.$toolUtil.storageGet("role")!="" && !context?.$toolUtil.storageGet("isAdmin")) {
				disabledForm.value.shouzhijieyu = true;
			}
			if(context?.$toolUtil.storageGet("role")!="" && !context?.$toolUtil.storageGet("isAdmin")) {
				disabledForm.value.dengjiriqi = true;
			}
		})
	}
	//
	//
	defineExpose({
		init
	})
	//
	const closeClick = () => {
		formVisible.value = false
	}
	//
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//
	const save= async ()=>{
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//
					changeCrossData(objcross)
				}else{
					crossUserId = user.value.id
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(async res=>{
								emit('formModelChange')
								context?.$toolUtil.message(``,'success')
                                formVisible.value = false
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(async (res)=>{
						emit('formModelChange')
						context?.$toolUtil.message(``,'success')
                        formVisible.value = false
					})
				}
			}
		})
	}
	//
	const changeCrossData = async (row)=>{
		await context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
</script>
<style lang="scss" scoped>
	// 
	.formModel_form{
		// form item
		:deep(.el-form-item) {
			//label
			.el-form-item__label {
			}
			// 
			.el-form-item__content {
				// 
				.list_inp {
				}
				//
				.list_date {
				}
				// 
				.el-textarea__inner {
				}
			}
		}
	}
	// 
	.formModel_btn_box {
		.cancel_btn {
		}
		.cancel_btn:hover {
		}
		
		.confirm_btn {
		}
		.confirm_btn:hover {
		}
	}
</style>
